<template>
  <div class="full-height navigation-wrapper">
    <v-row dense align="center" justify="center">
      <v-col cols="10">
        <v-text-field
          v-model="search"
          append-icon="fa-search"
          label="Search"
          single-line
          :loading="isFetchingData"
          hide-details
          class="mb-1"
        ></v-text-field>
      </v-col>
      <v-col cols="2">
        <tooltip-icon-button
          btnColor="default"
          btnIcon="fa-refresh"
          btnTooltip="Refresh Tree"
          buttonSize="medium"
          :onClick="() => fetchNetworkDevices()"
        >
        </tooltip-icon-button>
      </v-col>
    </v-row>

    <v-treeview
      class="text-left network-nav"
      v-if="routeLinks.length > 0"
      :items="routeLinks"
      :search="search"
      dense
      open-all
      hoverable
    >
      <template v-slot:label="{ item }">
        <div class="navigation-item-wrapper">
          <router-link :to="item.link" class="text-style" :active-class="currentUrl == item.link ? 'active-item' : ''">
            {{ item.name }}
          </router-link>
          <span v-if="item.count">{{ item.count }}</span>
        </div>
      </template>
    </v-treeview>
  </div>
</template>
<script>
import * as rfNetworkUiService from './rfNetworks.uiService';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import { rfNetworkService } from '../../../services/rfNetworkService.js';

export default {
  data: () => ({
    isFetchingData: false,
    currentUrl: '',
    search: '',
    networks: [],
    routeLinks: [],
  }),
  components: { TooltipIconButton },
  mounted() {
    this.fetchNetworkDevices();
  },
  watch: {
    '$route.path': {
      handler: function (path) {
        this.currentUrl = path;
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    updateNavigationNames(networks) {
      this.$store.commit('updateNetworksNavigationNames', networks);
    },
    fetchNetworkDevices() {
      this.isFetchingData = true;
      rfNetworkService
        .fetch({ with_devices: true })
        .then(data => {
          this.networks = data;
          this.constructRouteLinks();
          this.updateNavigationNames(this.networks);
        })
        .finally(() => {
          this.isFetchingData = false;
        });
    },
    routeLink(id, name, prefix) {
      return {
        id: id,
        name: `${prefix} ${name}`,
      };
    },

    networkDevicesLink: rfNetworkUiService.constructNetworkDevicesLink,
    devicePointsLink: rfNetworkUiService.constructDevicePointsLink,
    constructRouteLinks() {
      this.routeLinks = [
        {
          id: 1,
          name: 'All Networks',
          type: 'network',
          link: rfNetworkUiService.ROUTE_MAP.network,
          children: this.getTreeViewData(this.networks),
          count: this.networks.length,
        },
      ];
    },
    getTreeViewData(networks) {
      return networks.map(network => ({
        ...this.routeLink(network.uuid, network.name, 'Network:'),
        link: this.networkDevicesLink(network.uuid),
        type: 'devices',
        count: network.devices && network.devices.length,
        children:
          network.devices &&
          network.devices.map(device => ({
            ...this.routeLink(device.uuid, device.name, 'Device:'),
            type: 'points',
            link: this.devicePointsLink(network.uuid, device.uuid),
          })),
      }));
    },
  },
};
</script>
<style>
.network-nav .text-style {
  text-decoration: unset;
  color: unset;
  font-size: 0.8125rem;
  line-height: 1rem;
}

.full-height {
  height: 100%;
}

.active-item {
  font-weight: bold !important;
  font-size: 1rem !important;
}

.navigation-item-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation-wrapper {
  border-right: 1px solid #333;
  padding: 0.5em;
}
</style>


