import ROUTES from '../../../constants/routes';

export const ROUTE_MAP = {
  network: ROUTES.rfNetworks,
  devices: networkId => ROUTES.rfDevices.replace(':networkId', networkId),
  points: (networkId, deviceId) => ROUTES.rfPoints.replace(':networkId', networkId).replace(':deviceId', deviceId),
};

export const constructNetworkDevicesLink = networkId => {
  return ROUTE_MAP.devices(networkId);
};

export const constructDevicePointsLink = (networkId, deviceId) => {
  return ROUTE_MAP.points(networkId, deviceId);
};
